body{
    text-align: center;
    margin: 0px;
    padding: 0px;
    border: 0px;
    max-width: 100%;
    min-height:950px;
    max-height: 1370px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    -moz-font-feature-settings: initial;
    -webkit-font-feature-settings: normal;
    background: rgba(47, 1, 1, 0.11);


}
.center{
    width: 100%;
}

.center .subheader{
    font-size: 35px;
    text-align: center;

}

.center h1{
    text-align: center;
    font-size: 25px;
}

#sidebar{

    width: 40%;
    height: 380px;
    margin-top: 75px;
    margin-left:  480px;
    margin-bottom: 32px;
    padding: 8px;
    background: rgba(255, 255, 255, 0.842);
    color: #135081;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.308);
    font-size: 25px;
    font-weight:300;
    border-radius: 10px;
    transition: 1s;
}



#sidebar.archivosAdminCenter{

    float: center;
    margin-top: -380px;
    margin-left: 550px;
    min-height: 550px;
    height: auto;
    width: 900px;
    color: #135081;
}

#sidebar.archivosAdminCenter3{

    float: center;
    margin-left: auto;
    margin-right: auto;
    min-height: 350px;
    height: auto;
    width: 500px;
    color: #138153;
}



.btn{

    background: #6b00b3;
    color: rgb(255, 255, 255);
    padding:15px;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 25px;
    text-decoration: none;
    max-width: 1220px;
    transition: 200ms;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    float: center;
}


#btn-table{

    min-width: 210px;
    max-width: 210px;
    min-height: 60px;
    font-size: 20px;

}

.btn:hover{
    background: #2a0046;
    color: white;
}


/* Barrita*/

.table{
    padding-bottom: 15px;
    padding-top: 12px;
    margin-top: 1px;
    padding-right: 15px;
    padding-left: 15px;
    text-align: left;
    background: #6b00b3;
    max-width: 2100px; bottom: 1px solid #000000;
    min-width: 2000px;
    max-height: 10px;


}
.btn-navBar{
    text-decoration: none;
    padding: 6px;
    margin-right: 10px;
    float: center;
    color: #138153;
    font-weight: 100;
    font-size: 20px;
    box-shadow: none;
    background-color: rgba(255, 255, 255, 0.842);
    border: 0px;

}
.btn-navBar:hover{
    color: #2a0046;
    cursor: pointer;
}
